/* Simplified CSS */
.container {
  margin-top: 20px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.pageTitle {
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.table {
  margin-top: 20px;
}

.table th, .table td {
  text-align: center;
}

.buttonGroup {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.actionButton {
  font-size: 12px;
}

.actionButton + .actionButton {
  margin-left: 5px;
}

.statusBadge {
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 14px;
  color: white;
}

.statusBadge.active {
  background-color: #28a745;
}

.statusBadge.inactive {
  background-color: #dc3545;
}

.modalForm {
  padding: 20px;
}

.modalForm input, .modalForm select {
  width: 100%;
  margin-bottom: 15px;
  padding: 8px;
}
